'use strict';

import * as _ from 'lodash';

const DEFAULT = {
  'api': {
    'host': '3.0.184.8',
    'port': '80'
  },
  'app': {
    'port': 4000
  }
};

const DEVELOPMENT = {

};

const MOCK = {

};

const STAGING = {

};

const PRODUCTION = {
  'api': {
    'host': '3.0.184.8',
    'port': '80'
  }
};


const config: any = {
  DEVELOPMENT,
  MOCK,
  STAGING,
  PRODUCTION
};

const env: string = process.env.NODE_ENV.toUpperCase() || 'DEVELOPMENT';
const envConfig: any = config[env];
if (! envConfig){
  throw new Error(`NODE_ENV: ${env} is undefined.`);
}
const gConfig = _.merge(DEFAULT, envConfig);

export default gConfig;