import React from 'react';
import { useEffect, useRef, useState } from 'react';
import {gql, useQuery} from "@apollo/client";
import QuestionCard from './../questionCard';

const PER_PAGE: number = 5;

const fetchDataGQL = gql`
query TestQuery($limit: Float = 10, $offset: Float = 0) {
  __typename
  questions(limit: $limit, offset: $offset) {
    ...on IQuestion{
      title,
      body,
      answer
    },
    ...on SelectQuestion{
      options {
        key,
        value,
        correct
      }
    },
    ...on MultiSelectQuestion {
      options {
        key,
        value,
        correct
      }
    }
  }
}    
`;

const QuestionBook = (props: any) => {
    const [state, setState] = useState({
        questions: [],
        loading: true,
        page: 0,
        end: false
    })
    const loadMoreInspector = useRef(null);

    useEffect(() => {
        console.log('Current Page:' +  state.page);
        console.log('Total Questions:' + state.questions.length);

        const observer = new IntersectionObserver((entries) => {
            entries.forEach((e) => {
                if (e.intersectionRatio > 0 && !state.loading && !state.end) {
                    setState({...state, page: state.page + 1, loading: true});
                }
            })
        });
        if (loadMoreInspector.current) observer.observe(loadMoreInspector.current);
    });

    const {error, data} = useQuery(fetchDataGQL, {
        variables: {
            'limit': PER_PAGE,
            'offset': state.page * PER_PAGE
        },
        fetchPolicy: 'no-cache'
    });
    if (state.loading && data) {
        setState({
            ...state,
            'questions': state.questions.concat(data.questions),
            'loading': false,
            'end': data.questions.length < PER_PAGE
        });
    }
    if (error) {
        console.log('ERROR');
        console.log(error);
    }

    if (!state.questions || !state.questions.length ) {
        return <div>Loading</div>
    }
    const cards: JSX.Element[] = [];
    state.questions.map((val: any, idx: number) => {
        if (idx === state.questions.length -2) {
            cards.push(<div className='dumb' ref={loadMoreInspector} key='dumb'></div>);
        }
        cards.push(<QuestionCard q={val} key={idx}></QuestionCard>);
        return 0;
    })
    const endMark = <div>
        end of questions
    </div>
    return <div>
        {cards}
        {endMark}
    </div>
};
export default QuestionBook;