import React from 'react';
import { Card } from 'react-bootstrap';
import { Button } from '@mui/material';

import './index.css';
import { Question } from '../../../types';
import CheckBoxGroup from '../../common/CheckBox';
import RadioGroup from '../../common/RadioGroup';
import classNames from 'classnames';

type CardProp = {
    q: Question
};

type CardState = {
    user_answer: string,
    correct: boolean,
    reset: boolean,
    answered: boolean,
};

export default class QuestionCard extends React.Component<CardProp, CardState> {
    constructor(props: CardProp) {
        super(props);
        this.state = {
            user_answer: '',
            correct: false,
            reset: true,
            answered: false
        }
    }

    check(target:any) {
        let answer='';
        if (this.props.q.__typename === 'MultiSelectQuestion') {
            if (target.checked) {
                answer = this.state.user_answer + target.value;
            } else {
                answer = this.state.user_answer.replaceAll(target.value, '');
            }
            answer = answer.replaceAll(',', '').split('').sort().join(',');
        } else {
            answer = target.value;
        }
        this.setState({
            user_answer: answer,
            correct: this.props.q.answer === answer,
            reset: false
        })
    }

    submit(event:any) {
        const current = this.state.answered;
        this.setState({
            answered: !current,
            reset: current,
            user_answer: ''
        })
    }

    render(): React.ReactNode {
        const dynamicClass = classNames({
            'correct': this.state.correct,
            'answered': this.state.answered
        })
        return <Card className='question_card text-center m-3 p-3'>
            <Card.Title className='title'>Q: {this.props.q.title}</Card.Title>
            <Card.Body>
            {
                (this.props.q.__typename === 'SelectQuestion') &&
                <RadioGroup 
                    options={this.props.q.options}
                    reset={this.state.reset}
                    answered={this.state.answered}
                    onChange={this.check.bind(this)}
                ></RadioGroup>
            }
            {
                (this.props.q.__typename === 'MultiSelectQuestion') &&
                <CheckBoxGroup
                    options={this.props.q.options}
                    reset={this.state.reset}
                    answered={this.state.answered}
                    onChange={this.check.bind(this)}
                 ></CheckBoxGroup>
            }

                <div className={ 'feedback ' + dynamicClass }>
                    {this.state.correct ? 'CORRECT' : 'INCORRECT'}
                </div>

                <Button 
                    className = { 'btn_submit ' + dynamicClass }
                    variant="outlined"
                    onClick={this.submit.bind(this)}
                > 

                { this.state.answered ? 'Take Again' : 'Submit' }
                </Button>
            </Card.Body>
        </Card>
    }

}
