import React from "react";
import './RadioGroup.css';
import { Option } from '../../types';
import classNames from "classnames";

type RadioProps = {
  option: Option,
  key: number,
  checked: boolean
}

type RadioGroupProps = {
  options: Option[],
  reset: boolean,
  answered: boolean,
  onChange?: Function
};

type RadioGroupState = {
  checked: Map<string,boolean>
}
export default class RadioGroup extends React.Component<RadioGroupProps, RadioGroupState> {
  constructor(props: RadioGroupProps) {
    super(props);
    const checkedMap = new Map();
    props.options.map((o)=>{
      return checkedMap.set(o.key, false);
    })
    this.state = {
      checked: checkedMap
    }
  }
 
  onClick(event:any) {
    if (this.props.onChange) this.props.onChange(event.target);
    const newMap = new Map();
    this.props.options.map((o)=>{
      return newMap.set(o.key, false);
    })
    newMap.set(event.target.value, true);
    this.setState({
      checked: newMap
    })
  }

  Radio = (props:RadioProps) => {
    const dynamicClass = classNames({
      'correct': props.option.correct,
      'answered': this.props.answered,
      'checked': props.checked,
    });
    return (
    <label className={'radio ' + dynamicClass}>
      <input 
        type='radio'
        className='radio_input'
        onClick={this.onClick.bind(this)}
        value={props.option.key}
      ></input>
      <div className='radio_indicator'>
        {
          (( !this.props.reset && !this.props.answered && props.checked) || (this.props.answered && props.option.correct)) &&
          <svg viewBox='0 0 11 8' width='9' height='7' className={'checkmark ' + dynamicClass}>
            <title>checkmark</title>
            <path d="M9.6,0L11,1.3L3.9,8L0,4.3L1.4,3l2.6,2.4L9.6,0z"></path>
          </svg>
        }
        {
            (this.props.answered && !props.option.correct)  &&
            <svg viewBox='0 0 10 10' width='8' height='8' className={'exmark ' + dynamicClass}>
              <title>exmark</title>
              <path d="M5,4.17,9.17,0,10,.83,5.83,5,10,9.17,9.17,10,5,5.83.83,10,0,9.17,4.17,5,0,.83.83,0Z"></path>
            </svg>
          }
      </div>
      <div className='radio_label'> {props.option.value}</div>
    </label>
    )
  }

  render() {
    return (
      <div className="radio_group">
        {
          this.props.options.map((o,idx) => {
            return <this.Radio 
              key={idx}
              option={o}
              checked={this.state.checked.get(o.key) || false}
            ></this.Radio>
          })
        }
      </div>
    );
  }
}