import classNames from 'classnames';
import React from 'react';
import './CheckBox.css';
import { Option } from '../../types';

type CbProps = {
  option: Option,
  key: number,
  checked: boolean
}

type CbGroupProps = {
  options: Option[],
  reset: boolean,
  answered: boolean,
  onChange?: Function  
}

type CbGroupState = {
  checked: Map<string,boolean>
}

export default class CheckBoxGroup extends React.Component<CbGroupProps, CbGroupState> {
  emptyMap;
  constructor(props: CbGroupProps) {
    super(props);
    this.emptyMap = new Map();
    props.options.map((o)=> {
      return this.emptyMap.set(o.key, false);
    });
    this.state = {
      checked: new Map(this.emptyMap)
    }    
  }

  onChange(event: any) {
    let newMap: Map<string, boolean>;
    if (this.props.reset) {
      console.log('reset map');
      newMap = new Map(this.emptyMap);
    } else {
      newMap = new Map(this.state.checked);
    }
    const current = !(newMap.get(event.target.value) || false);
    newMap.set(event.target.value,current);
    this.setState({
      checked: newMap
    })
    if (this.props.onChange) this.props.onChange({
      checked: current,
      value: event.target.value
    });
  }

  CheckBox = (props:CbProps) => {
    const dynamicClass = classNames({
      'checked': props.checked,
      'answered': this.props.answered,
      'correct': props.option.correct
    });
    return (
      <label className={'checkbox ' + dynamicClass}>
        <input type="checkbox" className='cb_input' onChange={this.onChange.bind(this)} value={props.option.key}></input>

        <div className='cb_indicator'>
          {
            ((!this.props.reset && !this.props.answered && props.checked) || (this.props.answered && props.option.correct)) && 
            <svg viewBox='0 0 11 8' width='9' height='7' className={'checkmark ' + dynamicClass}>
              <title>checkmark</title>
              <path d="M9.6,0L11,1.3L3.9,8L0,4.3L1.4,3l2.6,2.4L9.6,0z"></path>
            </svg>
          }
          {
            (this.props.answered && !props.option.correct)  &&
            <svg viewBox='0 0 10 10' width='8' height='8' className={'exmark ' + dynamicClass}>
              <title>exmark</title>
              <path d="M5,4.17,9.17,0,10,.83,5.83,5,10,9.17,9.17,10,5,5.83.83,10,0,9.17,4.17,5,0,.83.83,0Z"></path>
            </svg>
          }
        </div>

        <div className='cb_label'> { props.option.value } </div>
      </label>
    );
  }

  render() {
    return (
      <div className="checkbox_group">
        {
          this.props.options.map((o,idx) => {
            return <this.CheckBox
              key={idx}
              option={o}
              checked={this.state.checked.get(o.key) || false}
            ></this.CheckBox>
          })
        }
      </div>
    )
  }
}